*,
*::after,
*::before {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

body {
  margin: 0;
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
.page {
  height: 100%;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
}

.container {
  padding: 0 20px;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}

.additionally__title {
  display: inline-block;
  position: relative;
  font-size: 24px;
  line-height: 24px;
  font-weight: 300;
  color: #000000;
  margin: 0 0 35px 0;
}
@media (min-width: 768px) {
  .additionally__title {
    font-size: 36px;
    line-height: 36px;
    padding: 0 0 0 36px;
    margin: 0 0 79px 0;
  }
}
@media (min-width: 1280px) {
  .additionally__title {
    padding: 0;
  }
}

.additionally__title::after {
  position: absolute;
  content: "";
  width: 100vw;
  height: 2px;
  background-color: #68b738;
  z-index: -1;
  left: calc(100% + 22px);
  top: 63%;
}
@media (min-width: 768px) {
  .additionally__title::after {
    left: calc(100% + 36px);
    top: 61%;
  }
}
@media (min-width: 1280px) {
  .additionally__title::after {
    top: 67%;
  }
}

@media (min-width: 768px) {
  .additionally__title::before {
    position: absolute;
    content: "";
    width: 100vw;
    height: 2px;
    background-color: #68b738;
    z-index: -1;
    right: 100%;
    top: 63%;
  }
}
@media (min-width: 1280px) {
  .additionally__title::before {
    right: calc(100% + 41px);
    top: 67%;
  }
}

.additionally__container {
  margin: 0 0 35px 0;
}
