.button {
  font-family: "Oswald", "Arial", sans-serif;
  display: block;
  border: 0;
  cursor: pointer;
  background-color: #68b738;
  text-align: center;
  font-size: 16px;
  line-height: 20px;
  width: 100%;
  padding: 10px 0;
  text-transform: uppercase;
  color: #ffffff;
  transition: 0.5s;
}

.button:hover {
  background-color: #5eaa2f;
}

.button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}

.button:focus-visible {
  color: rgba(255, 255, 255, 0.7);
  opacity: 0.5;
  outline: none;
}

.button:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.button[aria-disabled=true] {
  opacity: 0.7;
  pointer-events: none;
}

.button--second {
  font-size: 14px;
  line-height: 18px;
  padding: 12px 0 11px;
}
@media (min-width: 768px) {
  .button--second {
    font-size: 20px;
    line-height: 20px;
    padding: 17px 0 17px;
  }
}

.button--dim {
  background-color: #f2f2f2;
  color: #444444;
}

.button--dim:hover {
  background-color: #ebebeb;
}

.button--dim:active {
  background-color: #ebebeb;
  color: rgba(68, 68, 68, 0.3);
}

.button--dim:focus-visible {
  background-color: #ebebeb;
  color: rgba(68, 68, 68, 0.5);
  outline: none;
}

