.interview__label-radio {
  display: flex;
  align-items: center;
}

.interview__label-radio:hover .interview__control-mark,
.interview__label-checkbox:hover .interview__control-mark {
  border: 1px solid #bababa;
}

.interview__label-radio:active .interview__control-mark,
.interview__label-checkbox:active .interview__control-mark {
  border: 1px solid #444444;
}

.interview__label-radio:focus-within .interview__label,
.interview__label-checkbox:focus-within .interview__label {
  opacity: 0.5;
}

.interview__label-radio .interview__control-mark {
  border-radius: 50%;
}

.interview__label-checkbox {
  display: flex;
  align-items: center;
}

.interview__control-mark {
  position: relative;
  display: block;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  flex-shrink: 0;
}
@media (min-width: 768px) {
  .interview__control-mark {
    width: 40px;
    height: 40px;
  }
}

.interview__input-radio:checked + .interview__control-mark::after {
  content: "";
  position: absolute;
  width: 13px;
  height: 13px;
  background-color: #68b738;
  border-radius: 50%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .interview__input-radio:checked + .interview__control-mark::after {
    width: 18px;
    height: 18px;
  }
}

.interview__input-checkbox:checked + .interview__control-mark::after {
  content: "";
  position: absolute;
  background-image: url("../img/checked.svg");
  background-position: center;
  background-repeat: no-repeat;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  transition: 0.5s;
}
@media (min-width: 768px) {
  .interview__input-checkbox:checked + .interview__control-mark::after {
    background-image: url("../img/checked-tablet.svg");
  }
}

.interview__input-radio:disabled + .interview__control-mark,
.interview__input-checkbox:disabled + .interview__control-mark {
  border-color: rgba(186, 186, 186, 0.3);
  pointer-events: none;
}

.interview__input-radio:disabled + .interview__control-mark::after,
.interview__input-checkbox:disabled + .interview__control-mark::after {
  opacity: 0.3;
}

.interview__input-radio:disabled ~ .interview__label--control,
.interview__input-checkbox:disabled ~ .interview__label--control {
